interface IProps {
  endPoint: string;
  params?: {
    include?: string;
    filter?: string;
    order?: string;
  };
}

export const goFetch = {
  //these are the endPoint 'props' for Fetcher
  users: () => {
    return triggerFetch({
      endPoint: 'users/1'
    });
  },

  posts: () => {
    return triggerFetch({
      endPoint: 'posts',
      params: { include: 'tags' }
    });
  },

  tags: () => {
    return triggerFetch({
      endPoint: 'tags',
      params: { include: 'count.posts' }
    });
  },

  sections: () => {
    return triggerFetch({
      endPoint: 'posts',
      params: {
        filter: 'page:true',
        order: 'title dsc'
      }
    });
  }
};

const triggerFetch = ({ endPoint, params }: IProps) => {
  return fetch(
    window['ghost'].url.api(`${endPoint}`, {
      ...params
    })
  )
    .then(res => res.json())
    .then(data => {
      // console.log('data: ', data);
      // logic
      return {
        isLoading: false,
        data,
        hasErrored: false
      };
    })
    .catch(() => {
      return {
        isLoading: false,
        data: null,
        hasErrored: true
      };
    });
};



